}

.card img {
    width: 100%;
    height: 100%;
    opacity: 0.9;
}

p.card-desc {
    position: absolute;
    bottom: -1em;
    width: 100%;
    background: #505050;
    color: #fcfcfc;
    padding: 2px 4px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.showcase-container{
  height : calc(100vh - 130px);
  overflow: hidden;
  overflow-y: auto;
  background: #fff;
  border-radius: 3px;
}


